// export default Filters;
"use client";
import { useEffect, useState } from "react";

const Filters = ({ setJobs }) => {
  const [filters, setFilters] = useState({ keyword: "", location: "" });

  useEffect(() => {
    // Fetch or filter jobs based on the filters state
    const filteredJobs = []; // Replace with actual filtering logic
    setJobs(filteredJobs); // âœ… Updating state inside useEffect, not during render
  }, [filters, setJobs]); // Dependencies ensure it only runs when filters change

  const handleFilterChange = (e) => {
    setFilters({ ...filters, [e.target.name]: e.target.value });
  };

  return (
    <div>
      <input
        type="text"
        name="keyword"
        placeholder="Job title"
        value={filters.keyword}
        onChange={handleFilterChange}
      />
      <input
        type="text"
        name="location"
        placeholder="Location"
        value={filters.location}
        onChange={handleFilterChange}
      />
    </div>
  );
};

export default Filters;
