// export default Jobs;
"use client";

import { useState } from "react";
import Filters from "./Filters";

const Jobs = () => {
  const [jobs, setJobs] = useState([]);

  return (
    <section>
      <Filters setJobs={setJobs} />
      <div>
        {jobs.length > 0 ? (
          jobs.map((job) => <div key={job.id}>{job.title}</div>)
        ) : (
          <p>No jobs found</p>
        )}
      </div>
    </section>
  );
};

export default Jobs;
